import { useSelector } from 'react-redux';
import AOS from "aos"
import "aos/dist/aos.css"
import { useEffect } from "react";
import {Flex} from "@chakra-ui/react"
import html from "../image/icons8-html-5-48.png"
import css from '../image/icons8-css3-48.png'
import js from '../image/icons8-javascript-48.png'
import react from "../image/react.jpeg"
import redux from '../image/icons8-redux-48.png'
import node from '../image/icons8-node-js-48.png'
import set from "../image/icons8-settings-50.png"
import mongo from "../image/MongoDB_logo_01.png"
import express from "../image/express.png"
import typescript from "../image/typescript-1174965.png"
import npm from "../image/npm.png"
import dsa from "../image/dsa.jpeg"
import cypress from "../image/cypress.jpeg"
import vs from "../image/vscode.png"
import github from "../image/icons8-github-64 (1).png"
import chakra from "../image/chakra.png"
import git from "../image/git.png"
import boot from "../image/icons8-bootstrap-48.png"

import vercel from "../image/icons8-triangle-50.png"

import postman from "../image/icons8-postman-api-64.png"
import '../App.css';
import lightset from "../image/lightsetting.png"
import winter from "../image/icons8-winter-48.png"
import React from "react"
import "../CSS/skill.css"
function Skills(){
    const data=useSelector((store)=> store.theme)
    const [tech, setTech] = React.useState("allimg");
    useEffect(()=>{
        AOS.init({ duration: 1000 });
    },[])

    const skillsarr = [
        { img: html, name: "HTML",link:'https://developer.mozilla.org/en-US/docs/Web/HTML' },
        { img: css, name: "CSS",link:'https://developer.mozilla.org/en-US/docs/Web/CSS' },
        { img: js, name: "Java Script" ,link:'https://developer.mozilla.org/en-US/docs/Web/javascript'},
        { img: react, name: "React" ,link:'https://react.dev/'},
        { img: redux, name: "Redux" ,link:'https://redux.js.org/'},
        { img: mongo, name: "Mongo Db" ,link:'https://www.mongodb.com/'},
        { img: node, name: "Node JS",link:'https://nodejs.org/en' },
        { img: express, name: "Express JS",link:'https://expressjs.com/' },
        { img: typescript, name: "Type Script" ,link:'https://www.typescriptlang.org/docs/home'},
        { img: npm, name: "Npm",link:'https://www.npmjs.com/package/npm' },
        { img: dsa, name: "DSA" ,link:'https://www.geeksforgeeks.org/learn-data-structures-and-algorithms-dsa-tutorial/'},
        { img: cypress, name: "Cypress" ,link:'https://docs.cypress.io/guides/overview/why-cypress'},
      ];
      const frontend = [
        { img: html, name: "HTML" ,link:'https://developer.mozilla.org/en-US/docs/Web/HTML'},
        { img: css, name: "CSS" ,link:'https://developer.mozilla.org/en-US/docs/Web/CSS'},
        { img: js, name: "Java Script" ,link:'https://developer.mozilla.org/en-US/docs/Web/javascript'},
        { img: react, name: "React" ,link:'https://react.dev/'},
        { img: redux, name: "Redux" ,link:'https://redux.js.org/'},
        { img: typescript, name: "Type Script" ,link:'https://www.typescriptlang.org/docs/home'},
        { img: npm, name: "Npm" ,link:'https://www.npmjs.com/package/npm' },
        { img: dsa, name: "DSA",link:'https://www.geeksforgeeks.org/learn-data-structures-and-algorithms-dsa-tutorial/' },
        { img: cypress, name: "Cypress" ,link:'https://docs.cypress.io/guides/overview/why-cypress'},
      ];
      const backend = [
        { img: mongo, name: "Mongo Db",link:'https://www.mongodb.com/' },
        { img: node, name: "Node JS",link:'https://nodejs.org/en' },
        { img: npm, name: "Npm",link:'https://www.npmjs.com/package/npm' },
        { img: express, name: "Express JS" ,link:'https://expressjs.com/'},
        { img: dsa, name: "DSA" ,link:'https://www.geeksforgeeks.org/learn-data-structures-and-algorithms-dsa-tutorial/'},
      ];
      const toolsarr = [
        { img: github, name: "Github",link:'https://github.com/' },
        { img: git, name: "Git",link:'https://git-scm.com/' },
        { img: vs, name: "VS Code",link:'https://code.visualstudio.com' },
        {img:"https://th.bing.com/th/id/OIP._znGOAqvdFej5u-qCeVw_AHaHa?pid=ImgDet&rs=1",name:'CodeSandbox',link:'https://codesandbox.io/dashboard'},
        { img: chakra, name: "Chakra UI" ,link:'https://chakra-ui.com/'},
        {img:boot,name:'Bootstrap',link:'https://getbootstrap.com/'},
        {img:"https://seeklogo.com/images/N/netlify-logo-758722CDF4-seeklogo.com.png",name:'Netlify',link:'https://www.netlify.com/'},
        {img:vercel,name:'Vercel',link:'https://vercel.com/dashboard'},
        {img:postman,name:'Postman',link:'https://www.postman.com/'}

      ];

    return (<div style={{
    padding:'70px 10px',
    width:'100%',

    }}  id="skills">
        <div   data-aos="zoom-in-down"
        data-aos-duration="2000"
        data-aos-offset="100" style={{marginBottom:'50px'}} >
        <button id='heading' style={{backgroundColor:data?'transparent':'#4b8118',color:data?'black':'white'}}><h1  >Technical and Soft Skills</h1></button>
        </div>

      <Flex
        id="skillsButton"
        data-aos="zoom-in-down"
        data-aos-duration="2000"
        data-aos-offset="100"
        m="auto"
        w={400}
        mb={"60px"}
        justifyContent={"space-evenly"}
      >
        <button
        style={{borderRadius:'10px',padding:'5px 15px'}}
        
          className={tech === "allimg" ? "active" : null}
          onClick={() => setTech("allimg")}
        >
          ALL
        </button>
        <button
        style={{borderRadius:'10px',padding:'5px 20px'}}
          className={tech === "frontend" ? "active" : null}
          onClick={() => setTech("frontend")}
        >
          FRONTEND{" "}
        </button>
        <button
        style={{borderRadius:'10px',padding:'5px 20px'}}
          className={tech === "backend" ? "active" : null}
          onClick={() => setTech("backend")}
        >
          BACKEND
        </button>
      </Flex>



      <div className="App"   data-aos="zoom-in-down"
        data-aos-duration="2000"
        data-aos-offset="100"
         style={{marginTop:'70px',marginBottom:'90px'}}>
      <header className="App-header">
        <img src={winter} className="App-logo" alt="logo" />
        <img src={data?lightset:set}  className="App-logo2" alt="" />
      </header>
      </div>




      {tech==="allimg"?<div
        data-aos="flip-left"
        data-aos-easing="ease-out-cubic"
        data-aos-duration="3000"
        data-aos-offset="100"
        id="skillsdiv">
        {tech === "allimg"
          ? skillsarr.map((el) => (
              <div className='setdiv skills-card'>
                <a href={el.link}>
                <img className='set skills-card-img' alt="" src={el.img} />
                <h3 className='skills-card-name'>{el.name}</h3>
                </a>
              </div>
            ))
          : null}
        {tech === "frontend"
          ? frontend.map((el) => (
              <div className='setdiv skills-card' >
                <img className='set skills-card-img' alt="" src={el.img} />
                <h3 className='skills-card-name'>{el.name}</h3>
              </div>
            ))
          : null}
        {tech === "backend"
          ? backend.map((el) => (
              <div className='setdiv skills-card'>
                <a href={el.link}>
                <img className='set skills-card-img' alt="" src={el.img} />
                <h3 className='skills-card-name'>{el.name}</h3>
                </a>
              </div>
            ))
          : null}
      </div>:null}



      {tech==="backend"?<div
        data-aos="flip-left"
        data-aos-easing="ease-out-cubic"
        data-aos-duration="3000"
        data-aos-offset="100"
        id="skillsdiv"
      >
        {tech === "allimg"
          ? skillsarr.map((el) => (
              <div className='setdiv skills-card'>
                <a href={el.link}>
                <img className='set skills-card-img' alt="" src={el.img} />
                <h3 className='skills-card-name'>{el.name}</h3>
                </a>
              </div>
            ))
          : null}
        {tech === "frontend"
          ? frontend.map((el) => (
              <div className='setdiv skills-card'>
                <a href={el.link}>
                <img className='set skills-card-img' alt="" src={el.img} />
                <h3 className='skills-card-name'>{el.name}</h3>
                </a>
              </div>
            ))
          : null}
        {tech === "backend"
          ? backend.map((el) => (
            <div className='setdiv skills-card'>
              <a href={el.link}>
                <img className='set skills-card-img' alt="" src={el.img} />
                <h3 className='skills-card-name'>{el.name}</h3>
                </a>
              </div>
            ))
          : null}
      </div>:null}



      {tech==="frontend"?<div
        data-aos="flip-left"
        data-aos-easing="ease-out-cubic"
        data-aos-duration="3000"
        data-aos-offset="100"
        id="skillsdiv"
      >
        {tech === "allimg"
          ? skillsarr.map((el) => (
              <div className='setdiv skills-card'>
                <a href={el.link}>
                <img className='set skills-card-img' alt="" src={el.img} />
                <h3 className='skills-card-name'>{el.name}</h3>
                </a>
              </div>
            ))
          : null}
        {tech === "frontend"
          ? frontend.map((el) => (
              <div className='setdiv skills-card'>
                <a href={el.link}>
                <img className='set skills-card-img' alt="" src={el.img} />
                <h3 className='skills-card-name'>{el.name}</h3>
                </a>
              </div>
            ))
          : null}
        {tech === "backend"
          ? backend.map((el) => (
              <div className='setdiv skills-card'>
                <a href={el.link}>
                <img className='set skills-card-img' alt="" src={el.img} />
                <h3 className='skills-card-name'>{el.name}</h3>
                </a>
              </div>
            ))
          : null}
      </div>:null}


      
      <Flex
        id="Tools"
        data-aos="zoom-in-down"
        data-aos-duration="1000"
        data-aos-offset="100"
        margin={"auto"}
        paddingTop={'60px'}
        justifyContent={"space-around"}
      >
        <h1 style={{ color:data?"black": "white" ,fontFamily:'serif' ,fontSize:'47px'}}>Tools </h1>
      </Flex>
      <div id="skillsdiv">
        {toolsarr.map((el) => (
          <div
            data-aos="zoom-in-down"
            data-aos-duration="2000"
            data-aos-offset="100"
            className='setdiv skills-card'
          >
            <a href={el.link}>
            <img className='set skills-card-img' alt="" src={el.img} />
            <h3 className='skills-card-name'>{el.name}</h3>
            </a>
          </div>
        ))}
      </div>
    </div>
  )
}

export default Skills