import AOS from "aos"
import "aos/dist/aos.css"
import { useEffect } from "react";
import { useSelector } from 'react-redux';
import transpic from '../image/My project.png'
import pic from "../image/imresizer-1680623388621.jpg"
import '../CSS/about.css'
import { Summary } from "./summary";
function About() {
    const data = useSelector((store) => store.theme)
    useEffect(() => {
        AOS.init({ delay: 300 });
    }, [])
    return (
        <div id="about" className="about section">
            <div id='aboutsec'>
                <div data-aos="zoom-in" >
                    <button id='aboutme'>A Little Bit About Me</button>
                    <div width='100%'>
                        <p className="paragraph" style={{display:'block'}} >A professional web developer responsible for working both  on </p>
                        <p className="paragraph" >front-end and back-end development processes. Judicious and</p>
                        <p className="paragraph" > creative when crafting effective websites, apps, and platforms</p>
                        <p className="paragraph" > to propel competitive advantage and revenue growth. </p>
                        <p className="paragraph" > proficient in and analytical problem solver with a calm and</p>
                        <p className="paragraph" > focused demeanor.</p>
                        <p id="user-detail-intro" > HTML, CSS, JavaScript</p>


                    </div>
                </div>
                <div data-aos="zoom-in" >
                    <img className="home-img" style={{backgroundColor:data?'white':'white'}} src={data?transpic:transpic} alt='' />
                </div>
            </div>
            <div>
                <Summary />
            </div>


        </div>
    )
}
export default About