
import AOS from "aos"
import "aos/dist/aos.css"
import cathe from "../image/CATHERc.png"
import IKEA from "../image/IKEAc.png"
import SUGAR from '../image/SUGARcc.png'
import github from "../image/icons8-github-30.png"
import link from '../image/icons8-link-24.png'
import html from "../image/icons8-html-5-48.png"
import js from "../image/icons8-javascript-48.png"
import react from '../image/react.jpeg'
import redux from "../image/icons8-redux-48.png"
import chakra from "../image/chakra.png"
import css from '../image/icons8-css3-48.png'
import { useEffect } from 'react';
import { Link } from '@chakra-ui/react';
import '../CSS/project.css'
import taxtim from "../image/Screenshot (1238).png"
import { useSelector } from "react-redux"
function Project(){
    const data=useSelector((store)=> store.theme)
    useEffect(()=>{
        AOS.init({ duration: 1000 });
    },[])
    let projectdata={
        projects:[
            {
                image:taxtim,
                title:'TaxTim (CLONE)',
                type:'An online digital tax assistant',
                description:"TaxGenius is the cloned website of TaxTim , an online digital tax assistant that helps you to make tax pay easily. TaxGenius introduces a conversational approach with its simple and intuitive interface which helps individuals compute tax returns",
                texttackimg:[html,css,js,react,redux,chakra],
                Role:'Team Lead',
                texttack:["HTML","CSS","JavaScript","React","Redux","Chakra-Ui"],
                code:'CODE',
                codelink:`https://github.com/Payalsahuj/elastic-behavior-9682`,
                live:'LIVE',
                livelink:`https://elastic-behavior-9682.vercel.app/`
            },
            {
                image:SUGAR,
                title:'SUGAR (CLONE)',
                type:'An E-commers Website',
                description:"SUGAR is an E-commers website responsible for providing affordable cosmetics for women's. It is a leading Indian cosmetic brand that was created with the goal of providing high-quality, affordable makeup products to women everywhere.",
                texttack:["HTML","CSS","JavaScript","React","Redux","Chakra-Ui"],
                texttackimg:[html,css,js,react,redux,chakra],
                code:'CODE',
                codelink:`https://github.com/Payalsahuj/polite-sheep-7319`,
                live:'LIVE',
                livelink:`https://polite-sheep-7319.vercel.app`,
                Role:'An Individual project'
            },
            {
                image:cathe,
                title:'CATHERINES (CLONE)',
                type:'An E-commers Website',
                description:"Catherines is an E-commers website responsible for providing affordable plus size fashion products for women's to all over the world. Catherines was created with the goal of providing high-quality, affordable products to women everywhere.",
                texttackimg:[html,css,js],
                Role:'Team Lead',
                texttack:["HTML","CSS","JavaScript","JSON-Server"],
                code:'CODE',
                codelink:`https://github.com/Payalsahuj/punchy-crayon-507`,
                live:'LIVE',
                livelink:`https://spiffy-kulfi-5b4b0a.netlify.app`
            },
            {
                image:IKEA,
                title:'IKEA (CLONE)',
                type:'An E-commers Website',
                description:"IKEA is an E-commers website responsible for providing affordable home decore, furiture and various other stuffs to all over the world. IKEA, home furnishings retailer that was the world’s largest seller of furniture in the early 21st century, operating more than 300 stores around the world.",
                texttackimg:[html,css,js],
                Role:'Team Member',
                texttack:["HTML","CSS","JavaScript","JSON-Server"],
                code:'CODE',
                codelink:`https://github.com/Payalsahuj/-equal-slip-6381`,
                live:'LIVE',
                livelink:`https://phenomenal-brioche-666c78.netlify.app/`
            },
            
        ]
    }
    return (
        <div   id='projects'>
           <div data-aos="zoom-in-down"
        data-aos-duration="1000"
        data-aos-offset="100" id='projecthead'> <h1 >Projects</h1></div>
           <div style={{width:'85%'}}>{projectdata.projects.map((item)=>
            <div data-aos="zoom-in-down"
            data-aos-duration="2000"
            data-aos-offset="100" style={{backgroundColor:data?'white':''}} className="project-card" >
                <div id='imgdiv'><img src={item.image} alt="" /></div>
                <br/>
                <div id='descriptiondiv'>
                    <h1 className="project-title" >{item.title}</h1>
                    <br/>
                    <div>
                    <p style={{fontSize:'18px'}}>{item.type}</p>
                    <p className="project-description" style={{fontSize:'18px'}}>{item.description}</p>
                    <p style={{fontSize:'18px',color:'#C5E1A5'}}>{item.Role==="Team Lead" || item.Role==='Team Member'?'Role':'About'} : {item.Role}</p>
                    <div className="project-tech-stack">{item.texttackimg.map((ele)=> <img width={'50px'} src={ele} alt='' />)}</div>

                    <div className="project-tech-stack">{item.texttack.map((ele)=> <button data-aos="flip-left" style={{backgroundColor:'#004D40',color:'white',borderRadius:'5px',padding:'4px 8px'}}>{ele}</button>)}</div>
                    <div  id='linkcontainer'>
                    <Link className="project-github-link link" style={{textDecoration:'none'}} href={item.codelink} target='_blank'> <button data-aos="flip-left" className="livebutton" >Github<img style={{display:'block',width:'20px'}} src={github} alt="" /></button></Link>
                    <Link className="project-deployed-link link" style={{textDecoration:'none'}} href={item.livelink} target='_blank'> <button data-aos="flip-left" className="livebutton">Deployment<img style={{width:'14px',paddingTop:'3px'}} src={link} alt="" /></button></Link>
                    </div>
                    </div>
                </div>
            </div>
           )}</div>
           


        </div>
    )
}

export default Project