
import WithSubnavigation from "./navitem"
import { handeltheme } from '../redux/action';
import darkmood from "../image/darkicon.png"

import setting from '../image/icons8-gear-94.png'
import sun from '../image/icons8-sun-16.png'
import { useDispatch, useSelector } from "react-redux";
import '../CSS/theme.css'
function Navbar(){
    const data=useSelector((store)=> store.theme)
    const dispatch=useDispatch()
    function handletheme(){
        dispatch(handeltheme())
      }
    return (
        <div  id="nav-menu" >
            <WithSubnavigation/>
            <div className="theme" >
               <button onClick={handletheme} style={{borderRadius:'50%'}}><header className="theme-header"> <img style={{borderRadius:'50%',width:'50px'}} className="theme-logo"  src={data?darkmood:sun} alt="" /></header> </button> 
            </div>

            <div className="themetwo" >
               <button style={{borderRadius:'50%'}}><header className="theme-header"> <img style={{borderRadius:'50%',width:'90px'}} className="theme-logo"  src={data?setting:setting} alt="" /></header> </button> 
            </div>
        </div>
    )
}

export default Navbar