import { useSelector } from "react-redux"


export const Footer=()=>{
    const data = useSelector((store) => store.theme)
    return <div style={{color:data?'black':'white',padding:'15px 0px',textAlign:'left',fontSize:'15px'}}>
        <h1 >🫠 Made by Payal Sahu - Thank you for visiting.</h1>
        
    </div>
}